/* ==========================================================================
   Portfolio Single - Images layout style - begin
   ========================================================================== */

.qodef-portfolio-single-holder {
    
    &.qodef-ps-images-layout {

        .qodef-ps-info-holder {
            padding-top: 55px;
        }

        @include ipad-landscape {

            .qodef-ps-info-holder {
                padding-top: 0;
            }
        }

        .qodef-ps-info-item {
            margin: 0 0 16px;

            > h2 {
                margin: 0 0 14px 0;
                line-height: 43px;
            }

            > p {
                padding-left: 1px;
                line-height: 25px;
            }

            >h6.qodef-ps-info-title {
                font-size: 16px;
                color: $default-text-color;
                padding-left: 5px;
            }

            &:last-child {
                margin: 0 0 36px;
            }
        }

        .qodef-ps-custom-field {
            margin: 0 0 16px;

            > p {
                font-size: 19px;
                font-weight: 500;
                color: $default-heading-color;
                line-height: 18px;
                padding-left: 5px;
            }
        }

        .qodef-ps-categories {

            > a {
                font-size: 19px;
                font-weight: 500;
                color: $default-heading-color;
                line-height: 18px;
                padding-left: 5px;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .qodef-ps-social-share {

            .qodef-social-share-holder {

                .qodef-social-title {
                    margin-left: 5px;
                }

                > ul {
                    display: block;
                    margin-top: 3px;
                    margin-left: 2px;
                }
            }
        }
    
        .qodef-ps-image-holder {
            margin: 0 0 40px;
	        
            .qodef-ps-image {
                margin: 0 0 30px;

                a {
                    overflow: hidden;

                    img {
                        @include qodefTransition(transform 0.3s ease-out);
                    }

                    &:hover {

                        img {
                            @include qodefTransform(scale(1.03, 1.03));
                            @include qodefTransition(transform 0.3s ease-in);
                        }
                    }
                }
                
                &:last-child {
                    margin: 0;
                }
            }
        }

        .qodef-ps-navigation {
            margin: 56px 0 0;
        }
    }
}
/* ==========================================================================
   Portfolio Single - Images layout style - end
   ========================================================================== */