/* ==========================================================================
   Portfolio Item Layout - Gallery Overlay style - begin
   ========================================================================== */

.qodef-portfolio-list-holder {
	
	&.qodef-pl-gallery-overlay {
		
		&.qodef-pl-has-shadow {
			
			.qodef-pl-item-inner {
				box-shadow: $default-box-shadow;
			}
		}
		
		article {
			
			&:hover {

				.qodef-pli-image {

					img {
						@include qodefTransform(scale(1.03, 1.03));
						@include qodefTransition(transform .5s ease-in);
					}
				}
				
				.qodef-pli-text-holder {
					opacity: 1;
				}
			}
			
			.qodef-pl-item-inner {
				overflow: hidden;
			}

			.qodef-pli-image {

				img {
					@include qodefTransition(transform .5s ease-out);
					backface-visibility: hidden;
				}
			}
			
			.qodef-pli-text {
				
				.qodef-pli-title,
				.qodef-pli-excerpt {
					color: #fff;
				}
				
				.qodef-pli-category-holder {
					
					a {
						color: #fff;
						
						&:hover {
							color: $first-main-color;
						}
					}
				}
			}
		}
		
		.qodef-pli-text-holder {
			@include qodefAbsoluteHolderLayout();
			padding: 20px;
			background-color: rgba($default-heading-color, 0.4);
			opacity: 0;
			text-align: center;
			box-sizing: border-box;
			@include qodefTransition(opacity .2s ease-in-out);
		}
	}
}
/* ==========================================================================
   Portfolio Item Layout - Gallery Overlay style - end
   ========================================================================== */